import { useProductModal } from "@/contexts/productRegister-context";
// ...existing code...

export default function AddCategoryModal() {
  const { isOpen, closeModal, openModal } = useProductModal();
  const { refetch } = useCategories();
  const { addCategory, loading, error } = useAddCategory();

  const [name, setName] = useState("");
  const [success, setSuccess] = useState<string | null>(null);

  useEffect(() => {
    if (!isOpen("addCategory")) {
      setName("");
      setSuccess(null);
    }
  }, [isOpen]);

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    if (!name.trim()) return;
    try {
      await addCategory(
        { name: name.trim() },
        {
          onSuccess: (created) => {
            setName("");
            setSuccess("Category added");

            toast.success("Category added", {
              style: {
                '--normal-bg':
                  'color-mix(in oklab, light-dark(var(--color-green-600), var(--color-green-400)) 10%, var(--background))',
                '--normal-text': 'light-dark(var(--color-green-600), var(--color-green-400))',
                '--normal-border': 'light-dark(var(--color-green-600), var(--color-green-400))'
              } as React.CSSProperties
            });

            // close only the addCategory modal, keep addProduct open if it was open
            setTimeout(() => {
              closeModal("addCategory");
              setSuccess(null);
            }, 600);

            // refresh category list via SWR hook
            refetch?.();
          },
        }
      );
    } catch (err) {
      console.error("Add category failed (modal)", err);
    }
  }

  return (
    <Dialog open={isOpen("addCategory")} onOpenChange={(v) => { if (!v) closeModal("addCategory"); }}>
      <DialogContent className="sm:max-w-[420px]">
        <form onSubmit={handleSubmit}>
          <DialogHeader>
            <DialogTitle>Add Category</DialogTitle>
            <DialogDescription>Fill in the details below to add a new category.</DialogDescription>
          </DialogHeader>

          <div className="grid gap-4">
            <div className="grid gap-2">
              <Label htmlFor="category-name">Name</Label>
              <Input
                id="category-name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="Category name"
                required
                autoFocus
              />
            </div>

            {error && <div className="text-sm text-red-600">{error}</div>}
          </div>

          <DialogFooter className="mt-5">
            <Button variant="outline" type="button" onClick={() => closeModal("addCategory")}>
              Cancel
            </Button>
            <Button
              type="submit"
              disabled={loading}
              onClick={(ev) => {
                ev.preventDefault();
                handleSubmit(ev as unknown as React.FormEvent);
              }}
            >
              {loading ? "Saving..." : "Save"}
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
}
